<template>
  <div class="add-smoothie container">
    <h2 class="center-align indigo-text">Add smoothie</h2>
    <form @submit.prevent="addSmoothie">
      <div class="field">
        <label for="title">Smoothie Title</label>
        <input type="text" name='title' v-model='title'>
      </div>
      <div class="field" v-for="(ing, index) in ingredients" :key="index">
        <label for="ingredient">Ingredient:</label>
        <input type="text" name="ingredient" v-model="ingredients[index]">
        <i class="material-icons delete" @click="deleteIng(ing)" >delete</i>
      </div>
      <div class="field add-ingredient">
        <label for="add-ingredient">Press tab to add ingredient</label>
        <input type="text" name='add-ingredient' @keydown.tab.prevent="addIng" v-model="another">
      </div>
      <div class="field center-align">
        <p v-if="feedback" class="red-text">{{ feedback }}</p>
        <button class="btn pink">Add Smoothie</button>
      </div>
    </form>
  </div>
</template>

<script>
import db from '@/firebase/init';
import slugify from 'slugify';

export default {
  name: 'AddSmoothie',
  data() {
    return {
      title: null,
      another: null,
      ingredients: [],
      feedback: null,
      slug: null,
    };
  },
  methods: {
    addSmoothie() {
      if (this.title) {
        this.feeback = null;
        this.slug = slugify(this.title, {
          replacement: '-',
          remove: /[$*!_+~.()'"\-:@]/g,
          lower: true,
        });
        db.collection('smoothies').add({
          title: this.title,
          ingredients: this.ingredients,
          slug: this.slug,
        }).then(() => {
          this.$router.push({ name: 'Index' });
        }).catch((err) => console.log(err));
      } else {
        this.feedback = 'You must enter a smoothie title';
      }
    },
    addIng() {
      if (this.another) {
        this.ingredients.push(this.another);
        this.another = null;
        this.feedback = null;
      } else {
        this.feedback = 'You must enter a value to add an ingredient';
      }
    },
    deleteIng(ing) {
      this.ingredients = this.ingredients.filter((i) => i !== ing);
    },
  },
};
</script>

<style lang="scss">
div.add-smoothie {
  margin-top: 60px;
  padding: 20px;
  max-width: 500px;
  h2 {
    font-size: 2em;
  }
  .field {
    margin: 20px auto;
    position: relative;
  }
  .delete {
    color: red;
    position: absolute;
    bottom: 20px;
    right: 10px;
    cursor: pointer;
  }
}
</style>
